    display: inline-block;
    margin: 18px 0 0 50px;
}

.nav > li {
    display: table-cell;
}

.navbar-brand {
    padding-top: 10px;
}

.small-circle {
    width: 100px;
    border-radius: 50%;
    background: red;
    color: white;
    display: table-cell;
    width: 20px;
    height: 20px;
    text-align: center;
    vertical-align: middle;
    position: absolute;
    top: 18px;
    right: 17px;
}

@media (max-width: 767px) {
    .nav.navbar-nav.navbar-right {
        display: table;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }

    #bs-example-navbar-collapse-1 > div {
        width: 100%;
        display: block;
        margin: 15px 0;
    }
}

