/*arrow*/
.dropdown-menu:after {
    bottom: 100%;
    right: 25px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.dropdown-menu:after {
    border-bottom-color: #ffffff;
    border-width: 10px;
}

.dropdown-menu:before {
    border-bottom-color: #ffffff;
    border-width: 10px;
}

