.infinity-carousel > p {
    -moz-border-bottom-colors: none;
    -moz-border-left-colors: none;
    -moz-border-right-colors: none;
    -moz-border-top-colors: none;
    border-color: #d9dada !important;
    border-image: none;
    border-radius: 5px;
    border-style: ridge;
    border-width: 1px;
    color: #343837;
    font-family: arial-bold;
    margin: auto auto -4px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;

    font-size: 14px;
    width: 180px;
}

.infinity-carousel > p > span {
    font-family: arial-Regular;
}

/*slide*/

.infinity-carousel {
    background-color: #fff;
}

.infinity-carousel .nav {
    font-weight: bold;
    font-size: 2em;
}

.nav.prev {
    margin-left: 30px;
}

.nav.next {
    margin-right: 30px;
}

.infinity-carousel {
    position: relative;
    overflow: hidden;
}

.slides span {
    color: #343837 !important;
    display: inline-block;
    font-family: arial-regular !important;
    font-size: 12px !important;
    margin-top: 5px;
}

.infinity-carousel .no-transition {
    transition: none !important;
}

.infinity-carousel .nav {
    position: absolute;
    display: block;
    width: 15px;
    height: 30px;
    top: 50px;
    z-index: 50;
    border: 0;
    cursor: pointer;
    -webkit-apperance: none;
}

.infinity-carousel .nav:hover {
    opacity: 1;
}

.infinity-carousel .nav.next {
    right: 0;
}

.infinity-carousel .center {

    position: relative;
    width: 180px;
    height: 150px;
    margin: 0 auto;
}

.infinity-carousel .text {
    position: relative;
    width: 180px;
    height: 75px;
    margin: 0 auto;
    text-align: center;
    opacity: 0.1;
    transition: opacity 0.4s linear;
}

.infinity-carousel .active .text {
    opacity: 1;
}

.infinity-carousel .slides {
    background-color: #fff;
    position: absolute;
    padding-top: 15px;
    width: 100000px;
    height: 100%;
    transition: all 0.4s;
}

.infinity-carousel .slides > * {
    position: relative;
    float: left;
    width: 90px;
    height: 100%;
    text-align: center;
    overflow: hidden;
}

.bg-line {
    width: 100% !important;
    background-color: #f2f2f2;
}

